.header {
  margin-top: 9px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.links {
  margin-top: 6px;
  display: grid;
  grid-template-columns: min-content min-content;
  gap: 28px;
  align-items: center;
  font-size: 14px;
  line-height: 171%;
  letter-spacing: 0.4px;
  text-transform: uppercase;
}

.link_header {
  color: rgba(0, 0, 0, 0.87);
  font-weight: 500;
}

.link_selected {
  color: #9C27B0;
  border-radius: 4px;
  border: 1px solid rgba(156, 39, 176, 0.5);
  padding: 6px 15px;
  font-weight: 500;
}
